

<template>
<form @submit.prevent="createCategory(category)" class="form-inline" role="form">

    <div class="form-group">
        <label class="sr-only" for="category-edit"></label>
        <input v-model="category.name" type="text" class="form-control" id="category-edit" placeholder="">
    </div>

    <button type="submit" class="btn btn-default"></button>
    <a v-link="{name:'categories'}" class="btn btn-primary btn-link"></a>
</form>
</template>

<script>
export default {
    data() {
        return {
            category: {}
        }
    },
    methods: {
        createCategory: function(category) {
            this.$http.post('/api/categories' , category).then(function(response) {
                if (response.data == 1) {
                    swal('', '', 'success');
                    this.$router.go('/categories/');
                } else {
                    swal('', '', 'error');
                }
            });
        }
    }
}
</script>



