

<template>
<form @submit.prevent="createTag(tag)" class="form-inline" role="form">

    <div class="form-group">
        <label class="sr-only" for="tag-edit"></label>
        <input v-model="tag.name" type="text" class="form-control" id="tag-edit" placeholder="">
    </div>

    <button type="submit" class="btn btn-default"></button>
    <a v-link="{name:'tags'}" class="btn btn-primary btn-link"></a>
</form>
</template>

<script>
export default {
    data() {
        return {
            tag: {}
        }
    },
    methods: {
        createTag: function(tag) {
            this.$http.post('/api/tags' , tag).then(function(response) {
                if (response.data == 1) {
                    swal('', '', 'success');
                    this.$router.go('/tags/');
                } else {
                    swal('', '', 'error');
                }
            });
        }
    }
}
</script>



