<style scoped>

#file-content {
    display: none;
}
img{
    cursor: pointer;
}
img#showUploadFile {
    width: 200px;
    height: auto;
    display: block;
    margin: 10px 0 0 10px;
}
button#upload-btn{
    margin-bottom:10px;
}
.thumbnail img{
    width:242px;
    height:220px;
}

</style>

<template>

<div class="panel panel-default animated fadeIn">
    <div class="panel-heading"></div>
    <div v-show="imgBase64">
        <img :src="imgBase64" class="img-responsive" id="showUploadFile" />
    </div>
    <input @change="onFileChange" id="file-content" type="file">
    <div class="panel-body">
        <button @click="upLoadPhoto" id="upload-btn" class="btn btn-warning" :class="{'disabled' :uploading }">{{ uploading ? '...' : ''}}</button>
        <div class="row animated fadeIn">
            <div v-for="item in itemList" class="col-sm-6 col-md-4">
                <div class="thumbnail">
                    <img :src="baseUrl+item.key" @click="showUrl(item)"/>
                </div>
            </div>
        </div>
    </div>
</div>

</template>

<script>

export default {
    data() {
            return {
                photo: {},
                imgBase64: '',
                uploading: false,
                itemList: {},
                baseUrl: 'http://oewvb9bk1.bkt.clouddn.com/'
            }
        },
        created() {
            this.fecthPhoto();
        },
        methods: {
            fecthPhoto() {
                    this.$http.get('/api/photos').then(function(response) {
                        this.$set('itemList', response.data);
                    });
                },
                upLoadPhoto() {
                    $('#file-content').trigger('click');
                },
                showUrl(item){
                    swal('',item.key);
                },
                onFileChange(e) {
                    var vm = this;
                    new html5ImgCompress(e.target.files[0], {
                        before: function(file) {
                            // console.log('...');
                            // filefilterfile.type.indexOf('image') > -1
                            // return falsedonefailcomplate
                            if (file.type.indexOf('image') <= -1) {
                                swal('', '.PNG.JPG', 'error');
                                return false;
                            }
                        },
                        done: function(base64, file) {
                            // console.log('...');
                            // ajaxbase64
                            vm.$set('uploading', true);
                            var fd = new FormData();
                            fd.append('file', base64);
                            vm.$http.post('/api/upload', fd).then(function(response) {
                                if (response.data.ret_code === 'success') {
                                    vm.$set('photo', response.data.ret_msg);
                                    vm.$set('imgBase64', file);
                                    swal('', '', 'success');
                                } else {
                                    swal('', '', 'error');
                                }
                            });
                            vm.$set('uploading', false);
                        },
                        fail: function(file) {
                            // console.log('...');
                            swal('', '', 'error');
                            return false;
                        },
                        complate: function(file) {
                            // console.log('...');
                        },
                        notSupport: function(file) {
                            // console.log('')
                            // PCswfupload
                            swal('', '', 'error');
                            return false;
                        }
                    });
                }
        }
}

</script>



